<template>
    <div>
        <b-modal id="deactivePrintPartner" hide-footer hide-header centered>
            <template v-slot:modal-title>
                <h5 class="modal-title">Print Partner</h5>
            </template>
            <div class="d-block">
                <div class="modal-body mt-4 mb-3" align="center">
                    <div class="mt-4 mb-4">
                        <img v-if="PartnerStatus == 1" src="@/assets/images/icons/tick.png">
                        <img v-else src="@/assets/images/icons/warning-message-icon.png">
                    </div>
                    <h3>Are you sure</h3>
                    <p v-if="PartnerStatus == 1" class="mb-5">you want to activate this Print Partner?</p>
                    <p v-else class="mb-5">you want to deactivate this Print Partner?</p>
                    <button type="button" class="btn btn-outline-primary btn-lg mr-3" @click="$bvModal.hide('deactivePrintPartner')">Cancel</button>
                    <button type="button" class="btn btn-primary btn-lg" v-on:click="activeDeactiveStatus();">Ok</button>
                </div>
            </div>
        </b-modal>
        <div class="d-block header-section">
            <div class="py-3 px-3 align-self-center d-sm-flex w-100 rounded">
                <div class="w-sm-100 mr-auto">
                    <h1>
                        <b>Print Partner</b>
                    </h1>
                </div>
                <div class="float-right"> <button type="button" class="btn btn-link" v-on:click="loadnewPrintPartner()"><img src="@/assets/images/icons/add.png" alt=""> NEW PARTNER</button></div>
            </div>
        </div>
        <div class="container-fluid productManagement">
            <!-- START: Card Data-->
            <div class="row">
                <div class="col-12 col-md-12">
                    <div class="card ">
                        <div class="card-header p-0">
                            <b-collapse id="PrintPartner" v-model="showNewPrinter">
                                <div class="col-12 col-sm-11 ml-5 mr-4 mt-5">
                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Partner Name</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.name.$error }">
                                                    <input type="text" class="form-control" placeholder="Partner Name" v-model="newPrintPartner.name">
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.name.required">Partner Name is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newPrintPartner.name.checkDuplicate">Partner Name already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Registration Number</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.RegNumber.$error }">
                                                    <input type="text" class="form-control" placeholder="Registration Number" v-model="newPrintPartner.RegNumber">
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.RegNumber.required">Registration Number is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newPrintPartner.RegNumber.checkDuplicate">Registration Number already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Contact Name</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.contactName.$error }">
                                                    <input type="text" class="form-control" placeholder="Contact Name" v-model="newPrintPartner.contactName">
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.contactName.required">Contact Name is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Email Id</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.email.$error }">
                                                    <input type="text" class="form-control" placeholder="Email Id" v-model="newPrintPartner.email" :disabled="newPrintPartner.id!=0">
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.email.required">Email Id is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newPrintPartner.email.email">Emai id is not valid</span>
                                                        <span class="text-danger" v-else-if="!$v.newPrintPartner.email.checkEmailDuplicate">Emai id already exists</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Contact Number</label>
                                            <div class="col-sm-8">
                                                <div class="input-group mb-3">
                                                            <div class="form-group col-sm-4 p-0" :class="{ 'form-group--error': $v.newPrintPartner.countryCode.$error }">
                                                                <input type="text" class="form-control " placeholder="+233" v-model="newPrintPartner.countryCode">
                                                                <div v-if="addPrinterErrors">
                                                                    <span class="text-danger" v-if="!$v.newPrintPartner.countryCode.required">Country code is required</span>
                                                                </div>
                                                            </div>
                                                            <div class="form-group col-sm-8 pr-0" :class="{ 'form-group--error': $v.newPrintPartner.phone.$error }">
                                                                <input type="text" class="form-control" v-model="newPrintPartner.phone" placeholder="89654123656">
                                                                <p v-if="addPrinterErrors"> <span class="text-danger" v-if="!$v.newPrintPartner.phone.required">Contact Number is required</span></p>
                                                            </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-1 mb-4">
                                        <h6>Registered Address</h6>
                                    </div>
                                    <div class="form-row ml-3">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Address</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.address.$error }">
                                                    <textarea class="form-control" placeholder="Address" v-model="newPrintPartner.address"></textarea>
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.address.required">Address is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Country</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.country.$error }">
                                                    <select class="form-control" v-model="newPrintPartner.country">
                                                        <option value=""> Select Country</option>
                                                        <option :key="index" v-for="(country,index) in $store.getters.countries" :value="country.name">{{country.name}}</option>
                                                    </select>
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.country.required">Country is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newPrintPartner.country.checkNullOrEmpty">Country is not valid</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Postal Address</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.postalCode.$error }">
                                                    <input type="text" class="form-control" placeholder="Postal Address" v-model="newPrintPartner.postalCode">
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.postalCode.required">Postal Address is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-1 mb-4">
                                        <h6>Printing Facility Address</h6>
                                    </div>
                                    <div class="form-row ml-3">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Address</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.fltyAddress.$error }">
                                                    <textarea class="form-control" placeholder="Address" v-model="newPrintPartner.fltyAddress"></textarea>
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.fltyAddress.required">Address is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Facility Country</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.fltyCountry.$error }">
                                                    <select class="form-control" v-model="newPrintPartner.fltyCountry">
                                                        <option value=""> Select Country</option>
                                                        <option :key="index" v-for="(country,index) in $store.getters.countries" :value="country.name">{{country.name}}</option>
                                                    </select>
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.fltyCountry.required">Country is Required</span>
                                                        <span class="text-danger" v-else-if="!$v.newPrintPartner.fltyCountry.checkNullOrEmpty">Country is not valid</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Postal Address</label>
                                            <div class="col-sm-8">
                                                <div class="form-group" :class="{ 'form-group--error': $v.newPrintPartner.fltyPostalCode.$error }">
                                                    <input type="text" class="form-control" placeholder="Postal Address" v-model="newPrintPartner.fltyPostalCode">
                                                    <div v-if="addPrinterErrors">
                                                        <span class="text-danger" v-if="!$v.newPrintPartner.fltyPostalCode.required">Postal Address is Required</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <button class="btn btn-primary float-right mb-4 pl-5 pr-5 p-2 " name="save" value="SAVE" type="button" v-on:click="addUpdatePrinter()">Submit</button>
                                    <button class="btn btn-outline-primary float-right mb-4 pl-5 pr-5 mr-3 p-2" name="save" value="CANCEL" type="button" v-on:click="clearData()">CANCEL</button>
                                </div>

                                <div class="clearfix"></div>
                            </b-collapse>
                            <hr>
                            <div class="btn-group ml-5">
                                <button type="button" v-on:click="bindPartners('ALL')" class="btn btn-outline-dark rounded-btn mr-2">ALL({{allPrintersCount}})</button>
                            </div>
                            <div class="btn-group">
                                <button type="button" v-on:click="bindPartners('Active')" class="btn btn-outline-success rounded-btn mr-2">Active ({{activePrintersCount}})</button>
                            </div>
                            <div class="btn-group">
                                <button type="button" v-on:click="bindPartners('InActive')" class="btn btn-outline-danger rounded-btn mr-2">Inactive ({{inActivePrintersCount}})</button>
                            </div>
                            <hr>
                            <div class="col-12 col-sm-12 p-0">
                                <div class="table-responsive">
                                    <table class="table bordered" v-if="getPrintPartners.length>0">
                                        <thead>
                                            <tr>
                                                <th class="p-3">Name</th>
                                                <th class="p-3">Registration Number</th>
                                                <th class="p-3">Registration Date</th>
                                                <th class="p-3">Status</th>
                                                <th class="p-3">Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(printer,index) in getPrintPartners" :key="index">
                                                <td class="p-2">{{printer.name}}</td>
                                                <td class="p-2">{{printer.regNmbr}}</td>
                                                <td class="p-2">{{printer.regDate | FormatingDate}}</td>
                                                <td v-if="printer.status==1" class="text-success p-2">Active</td>
                                                <td v-else class="text-danger p-2">Inactive</td>
                                                <td class="p-2">
                                                    <!-- <a href="" class="pr-2">View</a> -->
                                                    <a v-on:click="editPrinter(printer.id)" class="text-primary"> <img src="@/assets/images/icons/edit-gray.png" /> Edit </a>
                                                    <a v-if="printer.status==1" class="text-danger" v-on:click="getActiveDeactiveStatus(printer.id,2)"><img src="@/assets/images/icons/activate.png" /> Deactivate</a>
                                                    <a v-else class="text-success" v-on:click="getActiveDeactiveStatus(printer.id,1)"><img src="@/assets/images/icons/deactivate-new.png" /> Activate</a>
                                                    <a class="text-info"> <img src="@/assets/images/icons/edit-gray.png" /> Configure </a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <nav aria-label="Page navigation example">
                                <ul class="pagination float-right mr-5 mb-5 mt-4">
                                    <!-- <li class="page-item"><a class="page-link" href="#">Prev</a></li>
                                    <li class="page-item active"><a class="page-link" href="#">1</a></li>
                                    <li class="page-item"><a class="page-link" href="#">2</a></li>
                                    <li class="page-item"><a class="page-link" href="#">3</a></li>
                                    <li class="page-item"><a class="page-link" href="#">Next</a></li> -->
                                </ul>
                            </nav>
                        </div>
                    </div>
                </div>
            </div>
            <!-- END: Card DATA-->
        </div>
    </div>
</template>
<script>
    import { required,email } from 'vuelidate/lib/validators';
    import { checkNullOrEmpty, checkEmailDuplicate } from '@/Validations';
    import toast from '@/toast';
    export default {
        data() {
            return {
                newPrintPartner: this.assignPrinterFields(), allPrintersCount: 0, activePrintersCount: 0,
                inActivePrintersCount: 0, addPrinterErrors: false, printersList: [],showNewPrinter:false, printerId:'', PartnerStatus:1
            };
        },
        async mounted() {
            await this.$store.dispatch("getCountriesAsync");
            await this.$store.dispatch("getPrintPartnersAsync");
            this.bindPartners('ALL');
        },
        computed: {
            getPrintPartners() {
                return this.printersList;
            }
        },
        validations: {
            newPrintPartner: {
                name: {
                    required,
                    checkDuplicate(value) {
                        if (this.$store.getters.printPartners.filter(element => element.name.toLowerCase() === value.toLowerCase()).length > 0) {
                            if (this.newPrintPartner.id !== 0) return true; return false;
                        }
                        return true;
                    }
                },
                RegNumber: {
                    required,
                    checkDuplicate(value) {
                        if (this.$store.getters.printPartners.filter(element => element.registerationNumber.toLowerCase() === value.toLowerCase()).length > 0) {
                            if (this.newPrintPartner.id !== 0) return true; return false;
                        }
                        return true;
                    }
                },
                contactName: { required },
                email: {
                    required,email,
                    checkEmailDuplicate(value) {
                        if (this.newPrintPartner.id !== 0) return true;
                            return checkEmailDuplicate(value);
                    }
                },
                phone: { required },
                address: { required },
                postalCode: { required },
                countryCode: { required },
                country: {required,checkNullOrEmpty},
                fltyAddress: { required },
                fltyPostalCode: { required },
                fltyCountry: {required,checkNullOrEmpty},
            }
        },
        filters: {
            FormatingDate: function (value) {
                if (value == null || value == '') return '';
                var date = new Date(value);
                var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
                return ('0' + date.getDate()).slice(-2) + "-" + months[date.getMonth()]  + "-" + date.getFullYear();
            }
        },
        components: {},
        methods: {
            assignPrinterFields() {
                var obj = { id: 0, name: '', RegNumber: '', contactId: 0, contactName: '', countryCode: '+91', email: '', phone: '', country: '', addressId: 0, address: '', postalCode: '', fltyAddressId: 0, fltyAddress: '', fltyPostalCode: '', orgFacilityId:0, facilityId:0, fltyCountry: '' };
                return obj;
            },
            loadnewPrintPartner() {
                this.newPrintPartner = this.assignPrinterFields(); this.addPrinterErrors = false;
                this.showNewPrinter=true;
            },
            async addUpdatePrinter() {
                this.$v.$touch();
                this.addPrinterErrors = this.$v.newPrintPartner.$anyError;
                if (!this.addPrinterErrors) {
                    var countryId = this.$store.getters.countries.filter(element => element.name === this.newPrintPartner.country).map(element => element.id);
                    var ftlycountryId = this.$store.getters.countries.filter(element => element.name === this.newPrintPartner.fltyCountry).map(element => element.id);
                    var objPrinter = {
                        id: this.newPrintPartner.id, name: this.newPrintPartner.name, registerationNumber: this.newPrintPartner.RegNumber,
                        status: 1, contact: {
                            email: this.newPrintPartner.email, id: this.newPrintPartner.contactId,
                            name: this.newPrintPartner.contactName, countryCode: this.newPrintPartner.countryCode, phone: this.newPrintPartner.phone
                        },
                        address: {
                            id: this.newPrintPartner.addressId, description: this.newPrintPartner.address, postalAddress: this.newPrintPartner.postalCode,
                            country: { id: parseInt(countryId), name: this.newPrintPartner.country, regions: null }
                        }
                    };
                    objPrinter.printFacility = [{
                        id: this.newPrintPartner.orgFacilityId, warehouse: {
                            id: this.newPrintPartner.facilityId, name: this.newPrintPartner.name + "_PrintFacility",
                            address: {
                                id: this.newPrintPartner.fltyAddressId, description: this.newPrintPartner.fltyAddress, postalAddress: this.newPrintPartner.fltyPostalCode,
                                country: { id: parseInt(ftlycountryId), name: this.newPrintPartner.fltyCountry, regions: null }
                            }
                        }
                    }];
                    this.newPrintPartner = this.assignPrinterFields();
                    await this.$store.dispatch("addPrintPartnerAsync", objPrinter);
                    toast.success("Print Partner Added!..");
                    this.showNewPrinter=false;
                    await this.bindPartners('ALL');
                }
            },
            editPrinter(printerId) {
                var obj = this.$store.getters.printPartners.filter(element => element.id === printerId);
                var currentPrinter = obj[0];
                this.newPrintPartner = {
                    id: currentPrinter.id, name: currentPrinter.name,
                    RegNumber: currentPrinter.registerationNumber, contactId: currentPrinter.contact.id, contactName: currentPrinter.contact.name,
                    email: currentPrinter.contact.email, phone: currentPrinter.contact.phone, countryCode: currentPrinter.contact.countryCode,
                    country: currentPrinter.address.country.name, addressId: currentPrinter.address.id, address: currentPrinter.address.description,
                    postalCode: currentPrinter.address.postalAddress
                };
                var facilityAddress = currentPrinter.printFacility[0];
                this.newPrintPartner.orgFacilityId = facilityAddress.id;
                this.newPrintPartner.facilityId = facilityAddress.warehouse.id;
                this.newPrintPartner.fltyAddressId = facilityAddress.warehouse.address.id;
                this.newPrintPartner.fltyAddress = facilityAddress.warehouse.address.description;
                this.newPrintPartner.fltyPostalCode = facilityAddress.warehouse.address.postalAddress;
                this.newPrintPartner.fltyCountry = facilityAddress.warehouse.address.country.name;
                this.showNewPrinter=true;
            },
            async activeDeactiveStatus() {
                await this.$store.dispatch("activateDeactivatePrintPartnerAsync", { orgId: this.printerId, orgStatus: this.PartnerStatus });
                this.$root.$emit('bv::hide::modal', 'deactivePrintPartner');
                await this.$store.dispatch("getPrintPartnersAsync");
                await this.bindPartners('ALL');
            },
            getActiveDeactiveStatus(id, status){
                this.printerId = id;
                this.PartnerStatus = status;
                this.$root.$emit('bv::show::modal', 'deactivePrintPartner');
            },
            clearData() {
                this.newPrintPartner = this.assignPrinterFields();
                this.showNewPrinter=false;
            },
            bindPartners(filterStatus) {
                var printers = []; this.printersList = [],
                    this.allPrintersCount = this.$store.getters.printPartners.length;
                this.activePrintersCount = this.$store.getters.activePrintPartners.length;
                this.inActivePrintersCount = this.$store.getters.inActivePrintPartners.length;
                switch (filterStatus) {
                    case 'ALL': {
                        this.$store.getters.printPartners.forEach(element => { printers.push(element) });
                        break;
                    }
                    case 'Active': {
                        this.$store.getters.activePrintPartners.forEach(element => { printers.push(element) });
                        break;
                    }
                    case 'InActive': {
                        this.$store.getters.inActivePrintPartners.forEach(element => { printers.push(element) });
                        break;
                    }
                }
                printers.forEach(element => {
                    var objPrinter = { id: element.id, name: element.name, regNmbr: element.registerationNumber, regDate: element.registerationDate, status: element.status };
                    this.printersList.push(objPrinter);
                });
            }
        }
    }
</script>